* {
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow: hidden;
}

button {
  opacity: 0.7;
  background: none;
  border: none;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
  color: #fff;
  font-weight: 600;
  cursor: pointer!important;
  background: rgba(255,255,255,0.05);
  padding: 10px 20px;
  border-radius: 5px;
}

button:hover {
  opacity: 1;
  color: #fff;
}

button:focus {
  outline: none;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
  background: #272727;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto,
    segoe ui, arial, sans-serif;
}

.Logo {
  color: #000;
  font-size: 240px;
  position: absolute;
  top: 43%;
  left: 40%;
  z-index: 1;
}

.Controls {
  position: absolute;
  z-index: 100;
  top: 30px;
  right: 30px;
}
.Stats {
  position: absolute;
  z-index: 100;
  top: 80px;
  right: 30px;
  background: rgba(255,255,255,0.02);
  padding: 20px;
  width: 250px;
  border-radius: 3px;
}

.Stat {
  display: -webkit-flex;
  display: flex;
  color: #fff;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-transform: uppercase;
  font-size: 17px;
  margin-bottom: 10px;
  font-weight: 600;
}

.Stat:last-child {
  margin-bottom: 0;
}

.Stat .title {
  
}

.Stat .value {
  -webkit-align-self: flex-end;
          align-self: flex-end;
}
